import React from 'react'
import styled from 'styled-components'
import { useContent } from '../content'
import calendarIcon from '../assets/calendar.svg'

export default function EmailForm() {
  const { emailForm: texts, contact } = useContent().content

  return (
    <Section className="hidden-container">
      <Content>
        <Info>
          <div>
            <h1>
              <strong>{texts.title}</strong>
            </h1>

            <p>{texts.subtitle}</p>
          </div>

          <BookADemoButton
            href={contact.bookADemo}
            target="_blank"
            rel="noreferrer"
            className="dark"
          >
            <img src={calendarIcon} alt="" />
            <span>{texts.bookDemo}</span>
          </BookADemoButton>
        </Info>

        <Person>
          <span>{texts.reachOut}</span>
          {texts.person.photo}

          <Name>
            <span className="underlined">{texts.person.name}</span>
            <span>{texts.person.position}</span>
          </Name>
        </Person>
      </Content>
    </Section>
  )
}

const Section = styled.section`
  display: flex;
  flex-direction: column;

  @media (min-width: 769px) {
    align-items: center;
    scroll-snap-align: center;
  }
`

const Content = styled.div`
  display: flex;
  flex-direction: column;
  gap: 64px;
  max-width: var(--max-outer-width);
  padding: var(--64) 16px;
  background: var(--turquoise);

  @media (min-width: 769px) {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
    width: 100%;
  }
`

const Info = styled.div`
  display: flex;
  flex-direction: column;
  gap: var(--32);

  @media (max-width: 768px) {
    > div > p > br {
      display: none;
    }
  }

  @media (min-width: 769px) {
    flex: 1;
    max-width: 820px;
    min-height: 302px;
  }
`

const Person = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  gap: var(--16);
  width: 100%;
  max-width: 400px;
  padding: 32px 8px;
  background: var(--white);
  border-radius: 8px;

  [data-gatsby-image-wrapper] {
    width: 190px;
    height: 190px;

    @media (max-width: 768px) {
      width: 170px;
      height: 170px;
    }
  }

  .underlined {
    text-transform: unset;
  }
`

const Name = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
`

const BookADemoButton = styled.a`
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  gap: 16px;
  width: 100%;
  max-width: 400px;
  padding: 15px 0;
  border-radius: 130px;
  background: var(--black);
  font: var(--900-16);
  transition: box-shadow 0.2s;

  @media (min-width: 769px) {
    max-width: 301px;
    align-self: auto;
    font: var(--900-21);
  }

  &:hover {
    &:hover:not(:disabled) {
      background: var(--black_07);
    }
  }
`
