import React from 'react'
import styled from 'styled-components'
import { useContent } from '../content'
import Section from '../components/Section'
import Arrow from '../components/Arrow'

export default function Contact() {
  const { contact: texts } = useContent().content

  return (
    <StyledSection className="hidden-container">
      <span className="underlined">{texts.underlined}</span>
      <h1>
        <strong>{texts.h1}</strong>
      </h1>
      <p>{texts.p}</p>

      <Items>
        <Item href={`tel:${texts.phone}`} className="glowing-border">
          <span className="underlined">
            {texts.phone.icon}
            {texts.phone.title}
          </span>
          <strong>{texts.phone.value}</strong>
        </Item>

        <Item href={`mailto:${texts.email.value}`} className="glowing-border">
          <span className="underlined">
            {texts.email.icon}
            {texts.email.title}
          </span>
          <strong>{texts.email.value}</strong>
        </Item>

        <Item
          href={texts.linkedIn.value}
          target="_blank"
          rel="noreferrer noopener"
          className="glowing-border"
        >
          <span className="underlined">
            {texts.linkedIn.icon}
            {texts.linkedIn.title}
          </span>
          <strong>
            {texts.linkedIn.label}
            <Arrow fill="var(--turquoise)" />
          </strong>
        </Item>
      </Items>
    </StyledSection>
  )
}

const StyledSection = styled(Section)`
  @media (min-width: 769px) {
    scroll-snap-align: center;
  }
`

const Items = styled.div`
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 64px;

  @media (min-width: 769px) {
    flex-direction: row;
  }
`

const Item = styled.a`
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 32px 16px;

  .underlined {
    display: flex;
    align-items: center;
    gap: 8px;
    text-transform: unset;

    @media (min-width: 769px) {
      svg {
        width: 48px;
        height: 48px;
      }
    }
  }

  strong {
    display: flex;
    align-items: center;
    gap: 8px;

    @media (min-width: 769px) {
      height: 48px;
    }

    &:hover svg {
      transform: translateX(50%);
    }

    svg {
      transition: transform 0.2s ease-in-out;
    }
  }
`
