import React from 'react'
import { styled } from 'styled-components'
import { useContent } from '../content'
import Section from '../components/Section'
import video from '../assets/animation.mp4'

export default function Efficiency() {
  const { efficiency: texts } = useContent().content

  return (
    <StyledSection id="efficiency">
      <Title>
        <span className="underlined">{texts.underlined}</span>
        <h1>{texts.h1}</h1>
      </Title>

      <ItemsWrapper>
        <VideoWrapper>
          <Video playsInline autoPlay muted loop>
            <source src={video} type="video/webm" />
          </Video>
        </VideoWrapper>

        <Items>
          {texts.efficiencyItems.map(({ title, text }, index) => (
            <Item key={index}>
              <h2>{title}</h2>
              <p>{text}</p>
            </Item>
          ))}
        </Items>
      </ItemsWrapper>
    </StyledSection>
  )
}

const StyledSection = styled(Section)`
  gap: 128px;

  @media (max-width: 768px) {
    h1 br {
      display: none;
    }
  }

  @media (min-width: 769px) {
    gap: 256px;
  }
`

const Title = styled.div`
  @media (min-width: 769px) {
    scroll-snap-align: center;
  }
`

const ItemsWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 128px;
  position: relative;
`

const Items = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 128px;

  @media (min-width: 769px) {
    gap: 0;
  }
`

const Item = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: var(--32);

  @media (min-width: 769px) {
    width: 50%;
    padding: 256px 0;
    scroll-snap-align: center;
  }

  p {
    max-width: 505px;
    margin-right: 32px;
    font-weight: 400;
  }
`

const VideoWrapper = styled.div`
  @media (min-width: 769px) {
    height: calc(100% + 80px);
    max-width: 50%;
    padding-top: 400px;
    position: absolute;
    right: 0;
  }
`

const Video = styled.video`
  display: block;
  margin: 0 auto;
  max-width: 100%;

  @media (min-width: 769px) {
    width: 610px;
    position: sticky;
    top: 50%;
    transform: translate(0, -50%);
    right: 0;
    z-index: 0;
  }
`
